.table {
	width: 100%;
	margin-bottom: 20px;
	border: 1px solid #dddddd;
	border-collapse: collapse;
  tr{
    text-align: center;
  }
  th {
    font-weight: bold;
    padding: 5px;
    background: #efefef;
    border: 1px solid #dddddd;
  }
  td {
    border: 1px solid #dddddd;
    padding: 5px;
  }
}
.registration{
  display:flex;
  justify-content: center;
}
